<template>
 <div class="kos">
     <div></div>
 </div>
</template>

<script type="text/ecmascript-6">
import { SPAN_V1 } from '@/assets/js/spanConverter.js'
import skew from '@/assets/js/skew.js'
import {
  traceSummary,
  traceSummariesToMustache
} from '@/assets/js/traceSummary.js'
import traceToMustache from '@/assets/js/traceToMustache'
export default {
  name: 'kos',
  data() {
    return {
      showData: [
        {
          title: '西安银行',
          item: [
            {
              id: 1,
              position: 1,
              name: ' 主机',
              content: '102.102.124'
            },
            {
              id: 2,
              position: 1,
              name: '服务器1',
              content: '102.102.124'
            },
            {
              id: 2,
              position: 2,
              name: ' 服务器2',
              content: '102.102.124'
            },
            {
              id: 2,
              position: 3,
              name: ' 服务器3',
              content: '102.102.124'
            },
            {
              id: 3,
              position: 1,
              name: ' 硬盘A',
              content: '102.102.124'
            },
            {
              id: 3,
              position: 2,
              name: ' 硬盘B',
              content: '102.102.124'
            },
            {
              id: 3,
              position: 3,
              name: ' 硬盘C',
              content: '102.102.124'
            }
          ]
        }
      ],
      book: [],
      datas: [
        {
          province: '浙江',
          city: '杭州',
          name: '西湖'
        },
        {
          province: '四川',
          city: '成都',
          name: '锦里'
        },
        {
          province: '四川',
          city: '成都',
          name: '方所'
        },
        {
          province: '四川',
          city: '阿坝',
          name: '九寨沟'
        }
      ]
    }
  },
  mounted() {
    // this.handledata()
    // this.getdata()
    // console.log(skew)
    // console.log(this.dele(this.datas, ['province', 'city', 'name']))
  },
  methods: {
    getdata() {
      this.$axios.get('http://localhost:9500/zipkin/span', {}).then(res => {
        let raw = res.data.ck
        // console.log(raw)
        const v1Trace = raw.map(SPAN_V1.convert)
        const mergedTrace = SPAN_V1.mergeById(v1Trace)
        const clockSkewCorrectedTrace = skew.correctForClockSkew(mergedTrace)
        const modelview = traceToMustache(clockSkewCorrectedTrace, 'allqiaojie')
        console.log(modelview)
        // this.zipkinTable = JSON.parse(ck)
      })
    },
    handledata(data, keys) {
      this.$axios.get('http://localhost:9500/zipkin/bar', {}).then(res => {
        let traces = res.data.book1
        // console.log(traces)
        const summaries = traces.map(raw => {
          const v1Trace = raw.map(SPAN_V1.convert)
          // console.log({'1': v1Trace})
          const mergedTrace = SPAN_V1.mergeById(v1Trace)
          // console.log({'2': v1Trace})
          const clockSkewCorrectedTrace = skew.correctForClockSkew(mergedTrace)
          // console.log({'3': traceSummary(clockSkewCorrectedTrace)})
          return traceSummary(clockSkewCorrectedTrace)
        })
        let cook = traceSummariesToMustache('all', summaries)
        console.log(cook)
      })
    },
    dele(tableData, keys) {
      let hashTable = {}
      let res = []
      for (let i = 0; i < tableData.length; i++) {
        let arr = res
        let cur = hashTable
        for (let j = 0; j < keys.length; j++) {
          let key = keys[j]
          let filed = tableData[i][key]
          if (!cur[filed]) {
            let pusher = {
              value: filed
            }
            let tmp
            if (j !== keys.length - 1) {
              tmp = []
              pusher.children = tmp
            }
            cur[filed] = { $$pos: arr.push(pusher) - 1 }
            cur = cur[filed]
            arr = tmp
          } else {
            cur = cur[filed]
            arr = arr[cur.$$pos].children
          }
        }
      }
      return res
    }
  }
}
</script>

